import {
  filtersTemplate,
  merchantsColumns,
  merchantsData
} from '@/admin/store/mock/merchantsMock'
import { crudStore } from '@/shared/plugins/vuex'
import { paginationCrud } from '@/admin/store/cruds/table'

export default crudStore([
  paginationCrud({
    tableName: 'table',
    data: merchantsData,
    columns: merchantsColumns,
    filters: filtersTemplate()
  })
], {
  namespaced: true,
  state: {
    currentMerchant: ''
  },

  mutations: {
    SET_CURRENT_MERCHANT (state, payload) {
      state.currentMerchant = payload
    }
  },

  getters: {
    getCurrentMerchant (state) {
      return state.currentMerchant
    }
  }
})
