<template>
  <AdminHeader>
    <Heading :to="{ name: ADMIN_ROUTES_NAMES.MERCHANTS }">{{ t('adminMerchants.title') }}</Heading>
    <template v-if="itemId" #breadcrumbs>
      {{ itemId }}
    </template>
  </AdminHeader>
  <section class="merchants">
    <keep-alive>
      <component
        :is="itemId ? 'MerchantsItem' : 'MerchantsGrid'"
      />
    </keep-alive>
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useRoute } from '@/shared/plugins/vue-router'
import { useGetters, useModule, useMutations } from '@/shared/plugins/vuex'
import { computed, defineAsyncComponent, watch } from 'vue'
import { ADMIN_ROUTES_NAMES } from '@/admin/common/routes'
import { MODULES_NAMES } from '@/shared/common/modules'
import Heading from '@/admin/components/Heading'
import AdminHeader from '@/admin/components/Header/AdminHeader'
import merchantsModule from '@/admin/store/modules/merchants'

export default {
  name: 'Merchants',
  components: {
    AdminHeader,
    Heading,
    MerchantsGrid: defineAsyncComponent(() => import('@/admin/views/Merchants/MerchantsGrid/MerchantsGrid')),
    MerchantsItem: defineAsyncComponent(() => import('@/admin/views/Merchants/MerchantsItem/MerchantsItem'))
  },
  setup () {
    const { t } = useI18n()
    const route = useRoute()
    useModule(MODULES_NAMES.MERCHANTS, merchantsModule)

    const {
      getCurrentMerchant
    } = useGetters(MODULES_NAMES.MERCHANTS)
    const {
      SET_CURRENT_MERCHANT
    } = useMutations(MODULES_NAMES.MERCHANTS)

    const itemId = computed({
      get: () => getCurrentMerchant.value,
      set: SET_CURRENT_MERCHANT
    })

    watch(() => route.params.id, () => {
      itemId.value = route.params.id || ''
    }, { immediate: true })

    return {
      t,
      itemId,
      ADMIN_ROUTES_NAMES
    }
  }
}
</script>

<style lang="scss">
.merchants {
  flex-grow: 1;
  padding-left: 40px;
  padding-top: 19px;
  padding-bottom: 65px;

  &__header {
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 36px;
  }
}
</style>
